@import "./styles/variables.scss";

.App {
  text-align: center;
  min-height: 100vh;

  .App-header {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background-color: map-get($colors, _green);

    // position:sticky;
    // bottom:0;
    // top:0;
    // right:0;
    // left:0;
    // z-index:2;
  }
  .filter-me {
    padding-top: 20px;
    padding-bottom: 60px;

    transition: all ease 0.3s;

    .videoTag {
      width: 100%;
      &.filter-video {
        filter: saturate(2.2);
      }
    }
    &:hover {
      filter: grayscale(1);
    }
  }

  .contact-us {
    background-color: map-get($colors, _yellow);

    .contact-us-content {
      background-color: white;
      width: 100%;
      max-width: 80%;
      border-radius: 10px;
      color: map-get($colors, _blue) !important;
      margin: 25px 0 40px 0;
      padding: 15px;
      text-align: left;
      box-shadow: 10px 10px 3px 1.25px rgba(245, 124, 124, 0.5);
      font-weight: 600;
    }
  }

  .products {
    background-color: map-get($colors, _green);
    .services-card-group {
      /* margin:auto;*/
      max-width: 80%;
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
  }

  .services {
    background-color: map-get($colors, _yellow);

    .row-services {
      max-width: 80%;
      width: 100%;
      justify-content: space-between;
      color: map-get($colors, _blue);

      .row-image-services {
        max-width: 300px;
        max-height: 300px;
        border-radius: 15px;
        box-shadow: -5px 5px 3px 1.25px rgba(245, 124, 124, 0.5);
      }

      .row-services-content {
        max-width: 60%;
        justify-content: center;
        text-align: left;
        align-items: center;
        display: flex;
        flex-direction: column;

        .row-services-content-title {
          width: 100%;
        }
      }
    }
  }

  .about-us {
    background-color: map-get($colors, _green);
  }
}

.title_text {
  text-align: left;
  margin: 15px 0;
  color: map-get($colors, _blue) !important;
  text-decoration: underline;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.25);
  max-width: 80%;
  width: 100%;
}

.space_between_sections {
  margin-bottom: 70px !important;
}

.decoration_line {
  margin: 20px 0;
  border-bottom: 5px solid white;
  max-width: 100%;
  width: 100%;
}

.green_line {
  margin: 10px 0;
  border-color: map-get($colors, _green) !important;
}

.section {
  max-width: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 0 40px 0;

  .social-media-buttons {
    box-shadow: 2px 2px 2px 1.25px rgba(0, 0, 0, 0.2);
    background: map-get($colors, _red);
    border-radius: 50%;
    max-width: map-get($sizes, button-size);
    max-height: map-get($sizes, button-size);
    min-width: map-get($sizes, button-size);
    min-height: map-get($sizes, button-size);
    transition: all ease-in-out 0.3s;
    &:hover {
      box-shadow: 5px 5px 5px 1.25px rgba(0, 0, 0, 0.2);
      border-radius: 50%;
      transform: scale(1.25, 1.25);
    }
  }
}

.social-media-bar {
  max-width: 20%;
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@import "./styles/card.scss";
@import "./styles/navbar.scss";
